// use server'

/**
 * @fileOverview Identifies the skills needed for future-proof careers based on current trends and expert forecasts.
 *
 * - identifyFutureSkills - A function that handles the identification of future skills.
 * - IdentifyFutureSkillsInput - The input type for the identifyFutureSkills function.
 * - IdentifyFutureSkillsOutput - The return type for the identifyFutureSkills function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const IdentifyFutureSkillsInputSchema = z.object({
  careerInterest: z
    .string()
    .describe('The users career interest or current job description.'),
  yearsOfExperience: z
    .number()
    .describe(
      'The number of years of experience the user has in their field, if any.'
    )
    .optional(),
});
export type IdentifyFutureSkillsInput = z.infer<typeof IdentifyFutureSkillsInputSchema>;

const IdentifyFutureSkillsOutputSchema = z.object({
  futureSkills: z
    .array(z.string())
    .describe('An array of skills that will be important for the user to develop in the future.'),
  explanation: z
    .string()
    .describe(
      'A detailed explanation of why these skills are important and how they relate to the user provided career interest.'
    ),
});
export type IdentifyFutureSkillsOutput = z.infer<typeof IdentifyFutureSkillsOutputSchema>;

export async function identifyFutureSkills(
  input: IdentifyFutureSkillsInput
): Promise<IdentifyFutureSkillsOutput> {
  return identifyFutureSkillsFlow(input);
}

const prompt = ai.definePrompt({
  name: 'identifyFutureSkillsPrompt',
  input: {schema: IdentifyFutureSkillsInputSchema},
  output: {schema: IdentifyFutureSkillsOutputSchema},
  prompt: `You are an AI career counselor specializing in identifying future-proof skills.

Given the following career interest and experience level, identify the skills that the user should develop to future-proof their career. Explain why these skills are important and how they relate to the user's career interest.

Career Interest: {{{careerInterest}}}
Years of Experience: {{{yearsOfExperience}}}

Skills:`,
});

const identifyFutureSkillsFlow = ai.defineFlow(
  {
    name: 'identifyFutureSkillsFlow',
    inputSchema: IdentifyFutureSkillsInputSchema,
    outputSchema: IdentifyFutureSkillsOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
